@media (min-width: 768px) {
  .hidden-tablet-up {
    display: none !important;
  }

  main {
    padding-left: 180px;
  }
}

@media (min-width: 1024px) {
  .hidden-desktop-up {
    display: none !important;
  }

  main {
    padding-left: 240px;
  }
}

@media (max-width: 767px) {
  .visible-tablet-up {
    display: none !important;
  }
}

@media (max-width: 1023px) {
  .visible-desktop-up {
    display: none !important;
  }

  .hidden-tablet-down {
    display: none !important;
  }
}
