@import "reset.css";
@import "normalize.css";
@import "../fonts/fonts.css";

body {
    font-family: 'DIN Pro', sans-serif;
    background: linear-gradient(to right, rgba(232, 232, 232, 1) 0%, rgba(255, 255, 255, 1) 100%);
}

#root {
    min-height: 100vh;
    display: flex;
    flex-flow: column nowrap;
}

#particles-js {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: -1;
}


